import React, { useContext } from 'react';
import SearchContent from './searchContext';
import { Dropdown, Tooltip } from '@platform/base';
import { getSysFieldid } from '@platform/api';
import SelectItem from './selectItem';

function SelectPlan() {
    let { search } = useContext(SearchContent);
    const { getCurrentPlan, setCurrentPlan, getStore, getJson, setPanelShow } = search.getInnerHooks();
    const schemeList = getStore(['querySchemes']);
    const currentPlanPk = getCurrentPlan();
    const currentPlanName = schemeList.query(currentPlanPk)['name'];
    const multiLang = getJson();
    return (
        <div className="search-edit">
            <Dropdown
                trigger={[
                    'click',
                ]}
                placement="bottomLeft"
                overlay={(
                    <div className="query_condo">
                        <ul className="query_content">
                            <div className={`searchPlanBox hideMore`}>
                                {schemeList.map(item => {
                                    return <p key={typeof item.pk_ncc_queryscheme == 'string' ? item.pk_ncc_queryscheme : '123456'} className="searchPlanList" fielid={`${item.name}_sub-btn`}>
                                        <SelectItem
                                            item={item}
                                            value={item.name}
                                            currentPlanPk={currentPlanPk}
                                            currentPlanName={currentPlanName}
                                            setCurrentPlan={setCurrentPlan}
                                            multiLang={multiLang}
                                            setPanelShow={setPanelShow}
                                            pk_ncc_queryscheme={item.pk_ncc_queryscheme}
                                        />
                                    </p>
                                })}
                            </div>
                        </ul>
                        <div
                            className="create_new"
                            onClick={() => {
                                setPanelShow(true, 'preset')
                            }}
                            fieldid={getSysFieldid('create_new_btn')}
                        >
                            <span className="new_content">{` + ${multiLang['page-search-0080']}`}</span>
                        </div>
                    </div>
                )}
                onVisibleChange={ () => {}}
                fieldid="search-plan"
            >
                <div className="search-name-wrapper" fieldid={getSysFieldid('search-plan_btn')}>
                    <span className="search-plan-name">{currentPlanName}</span>
                    <i className="iconfont icon-bottom" />
                </div>
            </Dropdown>
            <Tooltip
                delayShow={800}
                inverse
                overlay={<div style={{ padding: '10px' }}>F3</div>}
                placement="top"
            >
                <span
                    className="search-super"
                    onClick={() => setPanelShow(true)}
                >
                    {multiLang['page-search-0003']}
                </span>
            </Tooltip>

        </div>
    )
}

export default SelectPlan;