import React, { useState } from 'react';
import classnames from 'classnames';
import { Input, HotKeys } from '@platform/base';

function SelectItem (props) {

    // 确定按钮时 名称重复校验 空校验
    let handleConfirm = () => {
    }
    
    let searchAdvBtnClick = () => {
        props.setPanelShow(true, props.pk_ncc_queryscheme);
    } 
    let delSearchPlan = () => {

    } 
    let [ value, setValue ] = useState(props.value);
    let [ showFlag, setShow ] = useState(true) 
    const { item, multiLang = {}, currentPlanPk, currentPlanName, setCurrentPlan } = props;
    let iconClass = classnames({ 'icon-disable': item.isMyPlan || item.isquickqs });
    let itemClass = classnames({ 'item-current': item.pk_ncc_queryscheme == currentPlanPk });
    return (
        <div className={`query_item ${itemClass}`} >
            {showFlag ?
                (
                    <span className="search_planName" onClick={() => setCurrentPlan(item.pk_ncc_queryscheme)}>
                        {value}&nbsp;&nbsp;
                        {item.pk_ncc_queryscheme == currentPlanPk && item.name == currentPlanName ? <i className="iconfont icon-shenpitongguo" /> : null}
                    </span>
                ) : (
                    <HotKeys
                        keyMap={{
                            high: [
                                'enter',
                            ],
                        }}
                        handlers={{ high: handleConfirm }}
                    >
                        <Input
                            className="search_input"
                            autoFocus={true}
                            value={value}
                            onClick={e => {
                                e.stopPropagation();
                            }}
                            onChange={v => {
                                setValue(v);
                            }}
                        />
                    </HotKeys>
                )}
            {showFlag ? (
                <span className="search-plan-set">
                    <i
                        className={`iconfont icon-cxzhongmingming ${iconClass}`}
                        onClick={e => {
                            e.stopPropagation();
                            if (item.isMyPlan || item.isquickqs) return;
                            setShow(!showFlag );
                        }}
                    />
                    <i
                        className="iconfont icon-cxxiugai"
                        onClick={searchAdvBtnClick}
                    />
                    <i
                        className={`iconfont icon-cxshanchu ${iconClass}`}
                        onClick={e => {
                            e.stopPropagation();
                            if (item.isMyPlan || item.isquickqs) return;
                            delSearchPlan();
                        }}
                    />
                </span>
            ) : (
                <div className="search-list-button">
                    <span
                        className="button-item right"
                        onClick={handleConfirm}
                    >
                        {multiLang['page-search-0009']}
                    </span>
                    <span
                        className="button-item left"
                        onClick={e => {
                            e.stopPropagation();
                            setValue(props.value)
                            setShow(true)
                        }}
                    >
                        {multiLang['page-search-0010']}
                    </span>
                </div>
            )}
        </div>
    )
}

export default SelectItem;