import React, { Component } from 'react';
import { DragSource } from 'react-dnd';

const dragItemSource = {
    beginDrag(props) {
        return { attrcode: props.items.attrcode };
    },
}

const DragItem = ({ items, connectDragSource, onDragItemClick }) => {
    const { attrcode, label } = items;
    return connectDragSource(
        <div className="drag-item-wrapper" key={attrcode} onClick={onDragItemClick}>
            <div className="drag-item" title={label}>
                <span className="name">{label}</span>
            </div>
        </div>,
    ) 
}

export default DragSource('area', dragItemSource, (connect, monitor) => ({
    connectDragSource: connect.dragSource(),
    connectDragPreview: connect.dragPreview(),
    isDragging: monitor.isDragging(),
}))(DragItem)