import React from "react";
import { useSearch } from './store';
import SearchWrapper from './searchWrapper'
import HighPanel from './highPanel';
import SearchContent from './searchContext';
import { ItemProvider, BaseComponent } from '@platform/template';
import { getMultiLang } from "@platform/api";
import { setOperationOptionsMultiLang } from './utils';
@ItemProvider
class Search extends BaseComponent {
    constructor(props){
        super(props);
        // const { store, config, name } = props;
        // 实例化时 已知表单查询区ID 缓存ID和config
        // if(name && store) {
        //     const { setStore } = store.getInnerHooks();
        //     setStore(['searchId'], name);
        //     setStore(['propsConfig'], config)
        // }
    }

    UNSAFE_componentWillMount(){
        const { store } = this.props;
        const { setJson } = store.getInnerHooks()
        getMultiLang({ moduleId: 'page_search', callback: setJson });
    }

    render(){
        const { store, config = {} } = this.props;
        const { getStore } = store.getInnerHooks();
        let panelShow = getStore(['showHighPanel']);
        return (
            <SearchContent.Provider value={{ search: store, config }}>
                <div className="search-area">
                    {/* 外层 */}
                    <SearchWrapper 
                        search={store}
                        config={config}
                    />
                    {/* 高阶面板 */}
                    {panelShow && <HighPanel 
                        search={store}
                        config={config}
                    />}
                </div>
            </SearchContent.Provider> 
        )
    }
}

Search.useSearch = useSearch;

export default Search