import React, { useContext, Component } from "react";
import { getFunctionValueFromViewModal } from './utils';
import { isArray, isEmpty, getSysFieldid } from '@platform/api';
import { Tooltip, Select } from '@platform/base';
import Filed from './filed';

const Option = Select.Option;

function HighItem (props) {
    const {
        item, 
        data,
        currentPlanPk,
        currentPlanList,
        search,
    } = props;
    const { getCurrentHighPlan, getHighPanelStatus, getJson, setItemOperationSign, getSearchItemDom } = search.getInnerHooks();
    let currentHighPlan = getCurrentHighPlan();
    let status = getHighPanelStatus();
    let multiLang = getJson();
    let mustFillIn = null;
    let selectDom;
    if (item && item.required) {
        mustFillIn = <div className="mustFillIn">*</div>;
    }
    let logicOptions = {
        and: multiLang['page-search-0035'],
        or: multiLang['page-search-0036'],
    };
    if (data.children && data.children.length) {
        selectDom = (
            <div className="advancedSearchSign">
                <div
                    className="advancedSearchChild"
                >
                    {data.attrcode === 'root' ? (
                        <div className="advancedSearch_root">{logicOptions[data.operationSign]}</div>
                    ) : (
                        <div className="select-containter">
                            <Select
                                className="NC_logicSign"
                                showClear={false}
                                value={String(data.operationSign || 'and')}
                                onChange={() => {}}
                                fieldid={data.attrcode}
                            >
                                {Object.keys(logicOptions).map((val, index) => {
                                    return (
                                        <Option key={index} value={String(val)}>
                                            {logicOptions[val]}
                                        </Option>
                                    );
                                })}
                            </Select>
                        </div>

                    )}
                </div>
                {!data.noRemove &&
                    !data.isfixedcondition && (
                    <div className="searchAdvCom_remove" onClick={() => {}}>
                        {multiLang['page-search-0037']}
                    </div>
                )}
            </div>
        );
    } else {
        if (data.operationOptions) {
            selectDom = (
                <div
                    className="NC_searchAdvCom"
                    field={data.refpk}
                >
                    {
                        status === 'super' ? (
                            <span className='remove' >
                                <div className="link" />
                            </span>
                        ) : null
                    }
                    <span className='remove'>{data.remove ? (
                        <div className="searchAdvCom_remove" onClick={() => {}}>
                            <i className='iconfont icon-qingkong' />
                        </div>
                    ) : <div className="searchAdvCom_remove">{status === 'super' ? <div className="link" /> : null}</div>}
                    </span>
                    <span className='content'>
                        {mustFillIn}
                        <Tooltip
                            delayShow={800}
                            inverse
                            overlay={<div>{item.label}</div>}
                            placement="top"
                        >
                            <div className="searchAdvCom_name">{item.label}</div>
                        </Tooltip>

                        <div className="searchAdvCom_select">
                            {data.operationOptions.length > 1 ? (
                                <Select
                                    className="NC_operateSign"
                                    showClear={false}
                                    disabled={!!item.disabled}
                                    value={String(data.operationSign || multiLang['page-search-0035'])}
                                    onChange={(a, b) => setItemOperationSign('currentHighPlanMap', data.uniqueKey, a, b)}
                                    fieldid="operateSign"
                                >
                                    {data.operationOptions.map((val, index) => {
                                        return (
                                            <Option key={index} value={String(val.value)}>
                                                {val.display}
                                            </Option>
                                        );
                                    })}
                                </Select>
                            ) : (
                                <span className="option-label">{(data.operationOptions[0] || {}).display}</span>
                            )}
                        </div>
                        <div className="searchAdvCom_com">
                            <Filed
                                {...item}
                                key={item.attrcode}
                                currentPlanPk={currentPlanPk}
                                snapshotItem={data}
                                currentPlanList={currentPlanList}
                                planName={'currentHighPlanSuperMap'}
                            />
                        </div>
                    </span>

                </div>
            )
        }
    }
    if (data.children && data.children.length) {
        let logicOptions = {
            and: multiLang['page-search-0035'],
            or: multiLang['page-search-0036'],
        };
        const children = data.children;
        return (
            <React.Fragment>
                {selectDom}
                {children.map((item, index) => {
                    return <HighItem
                        item={getSearchItemDom(item.attrcode)}
                        key={`${item.attrcode}${index}`}
                        currentPlanPk={currentHighPlan}
                        data={item}
                        currentPlanList={children}
                        search={search}
                    />
                }).filter(item => item)}
            </React.Fragment>
            
        );
    }
    return selectDom
}

export default HighItem