import React, { useContext } from "react";
import { Item } from '@platform/template';
import SearchContext from './searchContext';
import { getFunctionValueFromViewModal } from './utils';
import { isArray, isEmpty, getSysFieldid } from '@platform/api';

const disabledOperationSign = ['is null', 'is not null'];

//获得查询条件的操作符的展示
function getQueryOperateDisplay(queryOperateType) {
    let result = '';
    if (queryOperateType) {
        queryOperateType = queryOperateType.replace(/^@+|@$/g, '');
        let opt = String(queryOperateType).split('@')[0];
        switch (opt) {
            case '<>':
                result = '≠';
                break;
            case '<':
                result = '<';
                break;
            case '>':
                result = '>';
                break;
            case '>=':
                result = '≥';
                break;
            case '<=':
                result = '≤';
                break;
        }
    }

    return result;
}

function Filed(props) {
    let {
        label,
        snapshotItem,
        planName,
        disabled,
        itemtype,
        style,
        ...others
    } = props;
    const { search, config } = useContext(SearchContext);
    const { onAfterEvent } = config;
    const { getItemValue, setItemValue } = search;
    const { getStore, getJson } = search.getInnerHooks();
    let multiLang = getJson()
    let showBetween = true;
    // 取disabled 判断
    if (disabledOperationSign.some(item => item === snapshotItem.operationSign)) {
        disabled = true;
    } 
    // 特殊类型处理 
    if (
        snapshotItem.hasOwnProperty('operationSign') &&
    		(itemtype === 'datepicker' || itemtype === 'NCTZDatePickerEnd' || itemtype === 'NCTZDatePickerStart')
    ) {
        if (snapshotItem.operationSign === 'between') {
            itemtype = 'rangepicker';
            showBetween = false;
        }
    } else if (itemtype === 'textarea') {
        itemtype = 'input';
    } else if (itemtype === 'radio' || itemtype === 'checkbox') {
        itemtype = 'select';
    }
    if(props.itemtype == 'residtxt'){
        others.onSelect = (residtxtValue, residtxtIndex) => {
            setItemValue(planName, snapshotItem.uniqueKey, {
                value: residtxtValue,
                index: residtxtIndex,
                display: residtxtValue,
            });
        }
    }
    return (
        <React.Fragment>
            <div className="search-item-wrap" 
                style={style}
                name={snapshotItem.attrcode}
                fieldid={getSysFieldid(snapshotItem.attrcode)}
            >
                <div className="condition-contant">
                    {props.required ? <span className="mustFillIn_search">*</span> : null}
                    <span className="search-dom">
                        <Item
                            {...others}
                            itemtype={itemtype}
                            disabled={disabled}
                            between={snapshotItem.operationSign == 'between' && showBetween}
                            placeholder={label}
                            key={snapshotItem.uniqueKey}
                            setFieldValue={({ value = {}, componentValue = {}, attrcode }) => {
                                let currentValue,
                                    resFlag = true;
                                switch (itemtype) {
                                    case 'refer':
                                        let referValue = componentValue
                                        if (Array.isArray(referValue)) {
                                            referValue.forEach(vv => {
                                                let VALUE = getFunctionValueFromViewModal({ display: vv.refname, value: vv.refpk }, getStore(['context']));
                                                vv.refname = VALUE.display;
                                                vv.refpk = VALUE.value;
                                                // referValue.runWithChildren = vv.runWithChildren;
                                                // referValue.refurl = vv.queryTreeUrl;
                                            }, this);
                                        } else {
                                            let VALUE = getFunctionValueFromViewModal({ display: referValue.refname, value: referValue.refpk }, getStore(['context']));
                                            // referValue.refname = VALUE.display;
                                            // referValue.refpk = VALUE.value;
                                        }
                                        currentValue = { ...value, ...componentValue } 
                                        break;
                                    case 'residtxt':
                                        currentValue = value;
                                        resFlag = false;
                                        setItemValue(planName, snapshotItem.uniqueKey, currentValue, attrcode)
                                        break;
                                
                                    default:
                                        currentValue = { ...value };
                                        break;
                                }
                                resFlag && setItemValue(planName, snapshotItem.uniqueKey, currentValue);
                                console.log(currentValue)
                            }}
                            getFieldValue={({ attrcode }) => {
                                let tempValue = getItemValue(planName, snapshotItem.uniqueKey),
                                    refDisplay = '';
                                // 分发值 逻辑处理
                                switch (itemtype) {
                                    case 'refer':
                                        if (snapshotItem.hasOwnProperty('operationSign') && snapshotItem.operationSign === 'between') {
                                            let VALUE = [];
                                            VALUE = (tempValue.value || []).map((v, index) => {
                                                let vv = {
                                                    value: v,
                                                    display: tempValue.display ? tempValue.display[index] : '',
                                                };
                                                return getFunctionValueFromViewModal(vv, getStore(['context']));
                                            });
                                            refDisplay = VALUE.map(v => v.display).join('~');
                                            let vv = {
                                                value: '',
                                                display: '',
                                            };
                                            tempValue = { ...tempValue, ...(isEmpty(VALUE) ? vv : VALUE[0]) }
                                        }else{
                                            if (Array.isArray(tempValue.value)) {
                                                tempValue = {
                                                    value: tempValue.value[0],
                                                    display: tempValue.display[0],
                                                };
                                            }
                                            let VALUE = getFunctionValueFromViewModal(tempValue, getStore(['context']));
                                            refDisplay = VALUE.display;
                                            tempValue = { ...tempValue, ...VALUE }
                                        }
 
                                        break;
                                    case 'select':
                                        if (!snapshotItem.isMultiSelectedEnabled) {
                                            if (snapshotItem.operationSign === 'between') {
                                                if (!Array.isArray(tempValue.value)) {
                                                    tempValue.value = [];
                                                }
                                            } else {
                                                tempValue.value = tempValue.value || '';
                                            }
                                        }
                                        break;
                                    case 'residtxt': 
                                        let valueMap = getItemValue(planName, snapshotItem.uniqueKey, 'valueMap') || {};
                                        tempValue = valueMap[attrcode] || {
                                            display: '',
                                            value: '',
                                        }
                                        
                                        break;
                                    default:
                                        tempValue.value = (tempValue || {}).value 
                                        // || (tempValue || {}).display;
                                        break;
                                }
                                if ((itemtype === 'datepicker' || itemtype === 'NCTZDatePickerEnd' || itemtype === 'NCTZDatePickerStart') && snapshotItem.operationSign === '=') {
                                    if (Array.isArray(tempValue.value)) {
                                        tempValue.value = tempValue.value[0];
                                    }
                                }
                                if(
                                    itemtype === 'rangepicker' ||
                                    ((itemtype === 'datetimepicker' || itemtype === 'NCTZDatePickerEnd' || itemtype === 'NCTZDatePickerStart') &&
                                        snapshotItem.operationSign === 'between')
                                ) {
                                    if (!Array.isArray(tempValue.value)) {
                                        if (tempValue.value) {
                                            tempValue.value = tempValue.value.split(',');
                                        } else {
                                            tempValue.value = [];
                                        }
                                    }
                                }
                                return tempValue;
                            }}
                            // onBeforeEvent={ (() => true)}
                            onAfterEvent={({ value, oldValue, componentValue, event, index }) => {
                                if (typeof onAfterEvent == 'function') {
                                    if(index === undefined) index = 0
                                    onAfterEvent(snapshotItem.attrcode, value, getStore(['searchId']), snapshotItem.operationSign, index, event);
                                }
                            }}
                            path={[snapshotItem.uniqueKey]}
                            showTimeFunction={true}
                            showClear={true}
                            showTime={false}
                            showToday={false}
                            dateInputPlaceholder={[
                                multiLang['page-search-0039'] + '(YYYY-MM-DD)',
                                multiLang['page-search-0040'] + '(YYYY-MM-DD)',
                            ]}
                            // multiLang={multiLang}
                        />
                    </span>
                </div>
                
            </div>
        </React.Fragment>
    );
}

export default Filed;