import React, { Component } from 'react';
import Filed from './filed';
import { Tooltip, Button, HotKeys, Dropdown, Checkbox } from '@platform/base';
import SelectPlan from './selectPlan';
import { 
    checkIsVisible, 
    templateFilter,
    getTimezoneOffset,
} from './utils'
require('./index.less')

class SearchWrapper extends Component {
    constructor(props){
        super(props)
        this.state = {};
    }
	
	countQueryConditions = (item, itemsMap) => {
	    switch (itemsMap.query(item.attrcode).itemtype) {
	        case 'refer':
	            return item.initialvalue.display;
	        case 'select':
	            return item.initialvalue.display;
	        case 'datepicker':
	            return item.initialvalue.display;
	        case 'NCTZDatePickerEnd':
	            return item.initialvalue.display;
	        case 'NCTZDatePickerStart':
	            return item.initialvalue.display;
	        case 'radio':
	            return item.initialvalue.display;
	        case 'checkbox':
	            return <Checkbox checked={!(item.initialvalue.value === 'N' || item.initialvalue.value === false)} disabled />;
	        case 'checkbox_switch':
	            return <Checkbox checked={!(item.initialvalue.value === 'N' || item.initialvalue.value === false)} disabled />;
	        case 'rangepicker':
	            return item.initialvalue.display;
	        case 'datetimepicker':
	            return item.initialvalue.display;
	        default:
	            return item.initialvalue.value;
	    }
	}

	//查询区目前展示方式：  1、只显示高级按钮； 2、都显示
	createSearchDom = () => {
	    const { search } = this.props;
	    let { getCurrentPlan, countUIScheme, getStore, setShowSearchItems, getJson } = search.getInnerHooks(),
	        currentPlanPk = getCurrentPlan(),
	        itemsMap = getStore('items'),
	        currentPlanList = countUIScheme(currentPlanPk),
	        allItem = [],
	        temp = [],
	        showSearchItems = getStore(['showSearchItems']),
		    multiLang = getJson();
	    if(!itemsMap) return null
	    currentPlanList.map(item => {
	        if (item) {
	            temp.push(item);
	            if (temp.length === 5) {
	                allItem.push([...temp]);
	                temp = [];
	            }
	        }
	    });
	    allItem.push(temp);
	    return (
	        <HotKeys
	            className="lightapp-component-search"
	            keyMap={{
	                query: [
	                    'enter',
	                ],
	                delete: ['Alt+Del'],
	            }}
	            handlers={{
	                delete: () => {
	                },
	                query: () => {
	                },
	            }}
	            attach={false}
	        >
	                
	            <div className={`${showSearchItems ? 'search-open-close' : ' '} search-top`}>
	                <SelectPlan />
	                {showSearchItems && (
	                    <Dropdown
	                        trigger={[
	                            'hover',
	                        ]}
	                        overlayClassName="search-dropdown"
	                        minOverlayWidthMatchTrigger={false}
	                        delayShow={0.8}
	                        // overlayClassName="multi-lang-text-collapse"
	                        placement="bottomLeft"
	                        overlay={
	                            currentPlanList.filter(item => item.initialvalue.value).length && (
	                                <div className="search-condition">
	                                    {currentPlanList.map(item => {
	                                        if (checkIsVisible(item)) {
	                                            return (
	                                                <p className="search-condition-item">
	                                                    <span className="item-title">{`${itemsMap.query(item.attrcode).label}: `}</span>
	                                                    <span className="item-content">{this.countQueryConditions(item, itemsMap)}</span>
	                                                </p>
	                                            );
	                                        }
	                                    })}
	                                </div>
	                            )
	                        }
	                    >
	                        <div
	                            className="search-conditions-value"
	                            onClick={() => {
	                                setShowSearchItems(true);
	                            }}
	                        >
	                            <span>
	                                {showSearchItems && (
	                                    <span>{multiLang['page-search-0025']}: (<span className="search-conditions-number">{currentPlanList.filter(item => checkIsVisible(item)).length}</span>)</span>
	                                )}&nbsp;&nbsp;
	                                {currentPlanList.map(item => {
	                                    if (checkIsVisible(item)) {
	                                        return itemsMap.query(item.attrcode).label + ';';
	                                    }
	                                })}
	                            </span>
	                        </div >
	                    </Dropdown>
	                )}
	                {(
	                    <div
	                        className="search-open"
	                        onClick={() => {
	                            setShowSearchItems(!showSearchItems)
	                        }}
	                    >
	                        {showSearchItems ? <span className="search-open-icon" ><i className="label">{multiLang['page-search-0087']}</i><i className="iconfont icon-chaxunmoren" /></span> : <span className="search-open-icon" ><i className="label">{multiLang['page-search-0088']}</i><i className="down iconfont icon-chaxunmoren" /></span>}
	                    </div>
	                )}
	            </div>
	            {!showSearchItems && <div className="item-contant">
	                {allItem.map((item, zindex) => {
	                    if (zindex === 0) item.push('button');
	                    return (
	                        <div key={zindex} className={`item-rows`}>
	                            {item.map((data, index) => this.createSearchItem(data, zindex > 0, index === item.length - 1, zindex, currentPlanList, index))}
	                        </div>
	                    );
	                })}
	            </div>}
	        </HotKeys>
	        
	    )
	};
	// 每一个item 
	createSearchItem = (snapshotItem, flag = false, buttonFlag, zindex, currentPlanList) => {
	    const { search } = this.props
	    let { getSearchItemDom, getCurrentPlan } = search.getInnerHooks();
	    if (buttonFlag && zindex === 0) {
	        return (
				
	            <div className="search-button">
	                {this.createButton()}
	            </div>
	        );
	    }
	    // 获取item  
	    let currentItem = getSearchItemDom(snapshotItem.attrcode)
	    return <Filed
	        {...currentItem}
	        style={{ marginTop: flag ? '8px' : '0' }}
	        currentPlanPk={getCurrentPlan()}
	        snapshotItem={snapshotItem}
	        currentPlanList={currentPlanList}
	        planName={'currentPlanMap'}
	    />
	}
	// 查询回调
	clickSearch = () => {
	    const { config = {}, search } = this.props;
	    const { getStore, getAllSearchDataEve } = search.getInnerHooks()
	    const { clickSearchBtn } = config
	    setTimeout(() => {
	        let snapshot = getStore(['showHighPanel']) ? getStore(['currentHighP`lanMap']) : getStore(['currentPlanMap']);
	        let data = getAllSearchDataEve(snapshot, true);
	        if (data) {
	            let template = templateFilter(getStore(['items']), snapshot);

	            let finalData = {
	                queryAreaCode: getStore(['searchId']),
	                oid: getStore(['oid']),
	                querytype: 'tree',
	                querycondition: data,
	                template,
	                nctz: getTimezoneOffset(),
	            };

	            if (clickSearchBtn && typeof clickSearchBtn == 'function') {
	                clickSearchBtn({ data, status: 'simple', finalData });
	            }
	        }
	    }, 0);
	};
	// 清空回调
	clearAllValue = () => {
	    const { config = {}, search } = this.props;
	    const { getStore, clearAllValueEve } = search.getInnerHooks()
	    const { advSearchClearEve } = config;
	    clearAllValueEve();
	    typeof advSearchClearEve == 'function' && advSearchClearEve(getStore(['currentPlanMap']))

	}
	// 生成查询、清空、高级 按钮
	createButton = () => {
	    const { config = {} } = this.props;
	    const {
	        onlyShowSuperBtn,
	        searchBtnName,
	        replaceSuperBtn,
	    } = config
	    return (
	        <div className='search-component-rowArea'>
	            {!onlyShowSuperBtn &&
					(
	                <Tooltip
	                    delayShow={800}
	                    inverse
	                    overlay={<div>{typeof searchBtnName == 'string' ? searchBtnName : '高级' }</div>}
	                    placement="top"
	                >
	                    <span
					            className={'search-component-searchBtn'}
					            onClick={this.clickSearch}
	                    >
	                        <i className="iconfont icon-sousuo" />
	                    </span>
	                </Tooltip>

	            )}
	            {!onlyShowSuperBtn &&
					(
	                <Tooltip
	                    delayShow={800}
	                    inverse
	                    overlay={<div style={{ padding: '0' }}>{`(Alt+Del)`}</div>}
	                    placement="top"
	                >
	                    <span
					            className="clearBtn"
					            onClick={this.clearAllValue}
	                    >
	                        <i className="iconfont icon-qingkong1" />
	                    </span>
	                </Tooltip>
	            )}
	            {onlyShowSuperBtn && (
	                <Tooltip
	                    delayShow={800}
	                    inverse
	                    overlay={<div>{replaceSuperBtn}</div>}
	                    placement="top"
	                >
	                    <Button
	                        style={{ maxWidth: onlyShowSuperBtn ? 'initial' : '29%' }}
	                        colors="primary"
	                    >
	                        {replaceSuperBtn}
	                    </Button>
	                </Tooltip>

	            )}
	        </div>
	    );
	};
	// 切换方案区域
	createCheckScheme = () => {
	    return (
	        <div
	            className="search-open"
	            onClick={() => {
	              
	            }}
	        >
	            <span className="search-open-icon" >
	                <i className="label"></i>
	                <i className="iconfont icon-chaxunmoren" /></span>
	        </div>
	    )
	}
	render(){
	    return (
	        <div className="search-area-contant">
	            <div className="NC_CreateSearch">
	                {this.createSearchDom()}
	            </div>
	        </div>
	        
	    )
	}
}

export default SearchWrapper
