import React, { Component } from "react";
import { DragSource, DropTarget } from 'react-dnd';
import { isFunction, getSysFieldid } from '@platform/api';
// import Filed from './filed';
import HighItem from './normalItem';

const areaTarget = {
    drop(props, monitor, component){
        const dragItem = monitor.getItem();
        console.log(props, dragItem, component);
    },
};

//DropTarget用于包装接收拖拽元素的组件，使组件能够放置
@DropTarget('area', areaTarget, (connect, monitor) => ({
    connectDropTarget: connect.dropTarget(),
    isOver: monitor.isOver(),
}))
class NormalArea extends Component {

    render(){
        const { search, connectDropTarget, addAdvBody } = this.props;
        const { getCurrentHighPlan, countHighUIScheme, getSearchItemDom } = search.getInnerHooks();
        let currentHighPlan = getCurrentHighPlan(),
            currentPlanList = countHighUIScheme();
        return (
            connectDropTarget(
                <div
                    className="normalArea"
                    id="advArea"
                    style={{ height: isFunction(addAdvBody) ? 'auto' : 'calc(100% - 40px)' }}
                >
                    <div
                        className="NC_searchAdvComBox"
                        fieldid={getSysFieldid('advSearch_tree-area')}
                    >
                        {
                            currentPlanList.map((item, index) => {
                                return <HighItem
                                    item={getSearchItemDom(item.attrcode)}
                                    key={`${item.attrcode}${index}`}
                                    currentPlanPk={currentHighPlan}
                                    data={item}
                                    currentPlanList={currentPlanList}
                                    search={search}
                                />
 
                            }).filter(item => item)
                        }
                    </div>
                </div>,
                
            )
        )
    }
}

export default NormalArea